.mobile-nav {
}
.mobile-nav.container {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;

  background: -webkit-gradient(linear, left top, left bottom, from(#a3f9fc), to(#1cd7f8));

  background: -webkit-linear-gradient(#a3f9fc, #1cd7f8);

  background: -o-linear-gradient(#a3f9fc, #1cd7f8);

  background: linear-gradient(#a3f9fc, #1cd7f8);
  height: 5%;
  width: 100%;
  z-index: 10;
}
.mobile-nav.pull-out {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 5px;
}
.mobile-nav.center-text {
  color: white;
  font-size: 1.4em;
}
.mobile-nav.right-spacer {
  padding-right: 5px;
}
.mobile-nav.right-spacer i {
  color: transparent;
}

.fas.fa-arrow-right {
  color: white;
  font-size: 2em;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.fas.fa-arrow-right.open {
  -webkit-transform: rotateZ(180deg);
      -ms-transform: rotate(180deg);
          transform: rotateZ(180deg);
}

.close-mobile-nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding: 5px 5px 0 0;
}



